.header {
    width: 100%;
    height: 80px;
}

.logo img {
    width: 20%;
}

.menu {
    margin-bottom: 0;
}

.nav__item a{
  text-align: center;
    text-decoration: none;
    color: black;
    font-weight: 600;
    font-size: 1.1rem;
}
.nav__item a:hover{
    color: inherit;
}

.nav__item a.active__link{
    color: #1097A3;
}


.right{
  display: flex;
  align-items: center;
  gap: 2em;
}

.mobile__menu{
    font-size: 1.3rem;
    color: black;
}

.sticky__header {
    width: 100%;
    height: 80px;
    line-height: 80px;
    background: #fff;
    position: sticky;
    top: 0;
    left: 0;
    box-shadow: 3px 3px 8px -3px #ddd;
    z-index: 999;
}


.hero__subtitle img{
  width: 10rem;
  height: 10rem;
  filter: drop-shadow(10px 1px 3px #747474);
}

.hero__subtitle img{
  width: 10rem;
  height: 10rem;
}

.hero__subtitle h3{
  color: #1097A3;
  font-family: cursive;
}
.hero_content h1{
  font-size: 3.2rem;
  font-weight: 600;
  margin-top: 1rem;
  margin-bottom: 2rem;
  color: black;
}

.hero_content h1 .highlights {
  color: #1097A3;
}

.hero_content p{
  font-size: 1rem;
  color: black;
  line-height: 2rem;
}

.hero__img-box img{
  width: 100%;
  height: 350px;
  border-radius: 20px;
  border: 1px #1097A3;
  object-fit: cover;
}

.hero__content img, .hero__img-box video{
  width: 100%;
  height: 350px;
  border-radius: 20px;
  object-fit: cover;
}

.hero__content{
  padding-top: 3.5rem;
}

.hero__img-box{
  padding-top: 2rem;
}


/* search box end here */


/* service section start here */
.services_subtitle{
  padding-top: 20%;
  font-size: 2.2rem;
  font-weight: 700;
  color: #1097A3;
  font-family: cursive;
}
.services_title{
  font-size: 2.5rem;
  font-weight: 500;
}

.service_item{
  padding-left: 13px;
  margin-top: 20%;
  margin-bottom: 10%;
  box-shadow: 10px 25px grey;
  border-radius: 0.5rem;
}

.service__img{
  width: 100px;
  height: 50px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50px;
  padding: 0.5rem;
  margin-bottom: 2rem;
}

.service__img img{
  width: 100%;
}

.service_item h5{
  font-size: 1.1rem;
  font-weight: 500;
}

.service_item p{
  font-size: 1rem;
  color: black;
  line-height: 2rem;
}
/* service section end here */


/* featured section start here */
.featured_tour-title h1{
  font-family: cursive;
  padding-top: 5%;
  color: #1097A3;
  font-size: 2.3rem;
  font-weight: 700;
}

.tour_img img{
  width: 100%;
  width: 100%;
  height: 350px;
  border-radius: 20px;
  border: 1px #1097A3;
  object-fit: cover;
  border-radius: 5px 5px 0 0;
}

.card {
  border: none !important;
  box-shadow: 0px 18px 50px -10px gray;
  
}

.tour_img {
  position: relative;
}

.tour_img span {
  position: absolute;
  bottom: 0;
  right: 0;
  width: max-content;
  height: max-content;
  color: #fff;
}

.card_top span i {
  color: #1097A3;
}

.tour_rating {
  font-size: 0.9rem;
  color: #1097A3;
}

.tour_location{
  color: #1097A3;
  font-weight: 500;
  font-size: 0.9rem;
}

.tour_rating i,
.tour_location i{
  font-size: 1rem;
}

.tour_title{
  margin-top: 1rem;
  font-size: 1.1rem;
  cursor: pointer;
}

.tour_title a{
  text-decoration: none;
  color: #000;
  transition: .3s;
}

.card_bottom h5{
  color: #1097A3;
  font-size: 1.1rem;
  font-weight: 700;
}

.booking_btn a {
  font-size: 0.9rem;
  color: #fff;
}
/* featured section end here */


/* experience section start here */

.experience_content h1{
  color: #1097A3;
  font-weight: 700;
  font-size: 2.5rem;
  font-family: cursive;
  padding-top: 30px;
}
.experience_content p{
  font-size: 1rem;
  color: black;
  line-height: 2rem;
  margin-top: 1rem;
}

.counter_wrapper{
  margin-bottom: 2.5rem;
}

.counter_box span{
  width: 70px;
  height: 70px;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #1097A3;
  font-size: 1.6rem;
  font-weight: 600;
  border-radius: 10px 5px 10px 5px;
}

.counter_box h6{
  font-size: 1.1rem;
  margin-top: .7rem;
  color: #000;
}

.experience_img img{
  width: 100%;
  margin-top: 40px;
}
/* experience section end here */



/* gallery section start here */

.gallery_content h1{
  color: #1097A3;
  font-weight: 700;
  font-size: 2.5rem;
  font-family: cursive;
  padding-top: 30px;
}

.gallery_content2 {
  font-size: 2.3rem;
  margin-top: 1rem;
}
.masonry_img{
  transition: 0.3s;
  cursor: pointer;
  width: 100%;
  object-fit: cover;

}

.masonry_img:hover{
  transform: scale(.9);
}
/* gallery section end here */



.testimonial_title{
  color: #1097A3;
  font-weight: 700;
  font-size: 2.5rem;
  font-family: cursive;
  padding-top: 30px;
}

.testimonial_content{
  font-size: 2.3rem;
  margin-top: 1rem;
  box-shadow: #000;
}


.testimonial p {
  width: 100%;
  font-size: 1rem;
  line-height: 2rem;
}




