
.tour{
    width: 100%;
    height: 100%;
}

.pagination span {
    width: 2rem;
    height: 2rem;
    padding: 5px;
    border-radius: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    border:  3px solid #1097A3;
    color: #000;
    font-size: 1.1rem;
    cursor: pointer;
    font-weight: 700;
}
.active_page {
    background: #1097A3;
    color: #fff !important;
}