.booking{
    padding: 2rem;
    border-radius: 0.5rem;
    border:  2px solid rgb(229, 231, 235);
}

.booking_top h3{
    font-size: 2rem;
    font-weight: 700;
}

.booking_top h3 span {
    font-size: 1.1rem;
    font-weight: 400;
}

.booking_top span {
    font-size: 1.1rem;
    font-weight: 600;
}

.booking_top span i {
    color: #1097A3;
}

.booking_top {
    padding-bottom: 2rem;
    border-bottom: 2px solid rgb(229, 231, 235);
}

.booking_form{
    padding-top: 2rem;
}

.booking_form h5{
    margin-bottom: 1rem;
}

.booking_form input{
    width: 100%;
    padding: 0.5rem;
    border-radius: .5rem;
    color: #2f6469;
    font-size: 1rem;
    border: none;
    border-bottom: 2px solid rgb(229, 231, 235);
}

.booking_info-form {
    padding: 1rem;
    border: 2px solid rgb(229, 231, 235);
}

.booking_bottom li{
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.booking_bottom li h5, .booking_bottom li span {
    font-size: 1rem;
    font-weight: 500;
}

.booking_bottom{
    margin-top: 1rem;
}

.total h5, .total span i {
    font-weight: 700 !important;
    font-size: 1.1rem !important;
}

.refund{
    padding: 18px;
    text-align: center;
    justify-content: center;
}