.main {
    padding: 10px;
    height: 100vh;
}

.layout{
    display: flex;
}

.sidebar {
    min-height: 100%;
    width: 300px;
    border-radius: 7px;
    background-color: rgb(51, 1, 1);
    box-shadow: 0 0 5px gray;
    margin-right: 20px;
    color: #fff;
}

.content {
    width: 100%;
    height: 100%;
}

.navbar{
    height: 10vh;
    margin-bottom: 20px;
    box-shadow: 0 0 5px gray;
    background-color: #fff;
}
.middle {
    height: 100vh;
    margin-bottom: 20px;
    box-shadow: 0 0 5px gray;
    background-color: #fff;
}

.logooo{
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.5rem;
    margin: 20px 0px;
}

.logooo img{
    width: 40%;
    height: 30%;
}

.menu{
    width: 100%;
}

.menu-item{
    margin-top: 60px;
}

.menu-item a {
    color: #fff;
    text-decoration: none;
    font-size: 1.2rem;
}

.menu-item i {
    font-size: 1.2rem;
    margin: 0 15px;
}

.active {
    width: 100%;
    background-color: #fff;
    padding: 10px;
}

.active a , .active i {
    color: rgb(51, 1, 1);
}