.newsletter{
    margin-top: 3rem;
    padding: 0;
    background: #cae5f4;
}

.newsletter_img img {
    width: 100%;
    filter: drop-shadow(20px 1px 13px #3f3e3e);
}

.newsletter_content {
    padding: 50px 0;
}

.newsletter_content p{
    line-height: 2rem;
}

.newsletter_content h2{
    font-size: 2.5rem;
    margin-bottom: 2rem;
}

.newsletter_input{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #fff;
    padding: 0.5rem;
    margin-bottom: 2rem;
    border-radius: 5px;
    width: 100%;
    padding: 0.7rem;
    border-radius: 2rem;
    border: 1px solid grey;
}

.newsletter_input input{
    width: 100%;
    padding: 0.5rem 0;
    border: none;
}

.newsletter_input input:focus {
    outline: none;
}

.newsletter_btnm{
    height: 45px;
    border-radius: 40px;
    background: #1097A3;
    width: 20%;
    padding: 0.5rem !important;
    background: #fff !important;
    color: #fff;
    font-size: 1.1rem !important;
}