.tour_content img{
    width: 100%;
    height: 459px;
    border-radius: 0.5rem;
    margin-bottom: 2.5rem;
}

.tour_info, 
.tour_reviews {
    border-radius: 0.5rem;
    padding: 2rem;
    border: 1px solid rgb(229, 231, 235);
}

.rating_group span{
    font-size: 1.1rem;
    display: flex;
    align-items: center;
    color: #1097A3;
    cursor: pointer;
}

.tour_reviews h4{
    margin-bottom: 3rem;
    
}

.review_input{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    padding: 0.7rem;
    border-radius: 2rem;
    border: 1px solid grey;
}

.review_input input {
    width: 100%;
    padding: 0.5rem 0;
    border: none;
}
.btnm {
    height: 45px;
    border-radius: 40px;
    background: #1097A3;
    width: 20%;
}
.review_input input:focus{
    outline: none;
}
.tour_info h2{
    font-size: 1.5rem;
    
}

.tour_info span{
    display: flex;
    align-items: center;
    column-gap: 0.5rem;
    font-style: 1rem;
    color: #000;
}

.tour_info span i {
    color: var(#1097A3);
    font-size: 1.1rem;
}

.tour_extra-details {
    display: flex;
    align-items: center;
    column-gap: 2.7rem;
    margin-top: 1rem;
    margin-bottom: 2.5rem;
    
}

.tour_extra-details2{
    margin-bottom: 2.5rem;
}

.near_vist{
    padding-top: 2rem;
    color: #1097A3;
    padding-bottom: 20px;
}

.near span i {
    font-size: 0.7rem;
    color: #1097A3;
}
.near span{
    display: flex;
    align-items: center;
    font-size: 1.1rem;
    font-weight: bold;
}
.near{
    padding-left: 20px;
}

.near p {
    padding-left: 20px;
}



.rule_regulation span  i {
    font-size: 0.4rem;
    text-align: center;
    justify-content: center;
    padding-left: 30px;
}

.rule_regulation span{
    padding-bottom: 10px;
}



.review_item{
    display: flex;
    padding-top: 10px;
    align-items: center;
    column-gap: 1rem;
    margin-bottom: 2rem;
}

.review_item img {
    width: 3rem;
    height: 3rem;
    border-radius: 50px;
    object-fit: cover;
}

.user_reviews{
    margin-top: 2.5rem;
}

.review_item h5{
    font-size: 1rem;
    margin-bottom: 0;
}

.review_item p{
    font-size: 0.9rem;
}

.review_item h6{
    font-size: 1.1rem;
}

.review_item span i {
    font-size: 1.2rem;
    color: #1097A3;
}

.review_item span {
    font-weight: 500;
}


.section{
    padding-top: 50px;
}

.section p{
    font-size: 1rem;
    font-weight: 700;
}

.section h2{
    font-size: 1.8rem;
    color: #1097A3;
    display: flex;
}

.what_wont h6{
    padding-top: 30px;
    font-size: 1.4rem;
    font-weight: normal;
}

.what_wont span{
    font-size: 1rem;
    text-align: center;
}

.what_wont span i {
    text-align: center;
    font-size: 1rem;
    color: #5cb85c;
    margin: 10px;
}


.what_wont2 h6{
    padding-top: 30px;
    font-size: 1.4rem;
    font-weight: normal;
}

.what_wont2 span i  {
    color: #d9534f;
    text-align: center;
    margin: 10px;
    font-size: 1rem;
}


