@import url('https://fonts.googlepis.com/css2?family=Popins:wght@300;400;500;600;700;800;900&display=swap');

.form-container{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    
}

 .wrapper {
    position: relative;
    width: 750px;
    height: 450px;
    background: #fff;
    border: 2px solid #1097A3;
    box-shadow: 0 0 65px ;
    overflow: hidden;
}


.wrapper .form-box{
    position: absolute;
    top: 0;
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.wrapper .form-box.register{
    left: 0;
    padding: 0 40px 0 40px;
}

.wrapper .form-box.login{
    right:0;
    padding: 0 40px 0 40px;
}

.form-box h1{
    font-size: 32px;
    text-align: center;
}


.form-box .input-box {
    position: relative;
    width: 100%;
    height: 30px;
    margin: 25px 0;
}




.input-box input {
    width: 100%;
    height: 100%;
    background: transparent;
    border: 2px solid #000;
    padding-right: 25px;
    font-size: 16px;
    color: #000;
    font-weight: 500;
    outline: none;
}



.input-box label {
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    font-size: 16px;
    color: #000;
    pointer-events: none;
}


.input-box i {
    padding: 15px;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    font-size: 18px;
    color: #000;
}



.btn {
    position: relative;
    width: 100%;
    height: 45px;
    background: #1097A3;
    outline: none;
    border-radius: 40px;
    cursor: pointer;
    font-size: 16px;
    color: #000;
    font-weight: 600;
    z-index: 1;
    overflow: hidden;
}


.btn::before{
    content: '';
    position: absolute;
    top: -100%;
    left: 0;
    width: 100%;
    height: 300%;
    background: linear-gradient(#1097A3, #1097A3, #1097A3, #1097A3);
    z-index: -1;
    transition: .5s;
}

.btn:hover::before{
    top: 0;
}

.form-box .logreg-link{
    font-size: 15px;
    color: #000;
    text-align: center;
    margin: 20px 0 10px;
}

.logreg-link p a {
    color: #1097A3;
    text-decoration: none;
    font-weight: 600;
}

.logreg-link p a:hover {
    text-decoration: none;
} 


.wrapper .info-text {
    position: absolute;
    top: 0;
    width: 50%;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.wrapper .info-text.register{
    right: 0;
    text-align: right;
    padding: 0 30px 70px 70px;
}
.wrapper .info-text.login{
    left: 0;
    text-align: left;
    padding: 0 30px 70px 30px;
}

.info-text h1{
    font-size: 36px;
    color: #000;
    line-height: 1.3;
    text-transform: uppercase;
}

.info-text p{
    font-size: 16px;
    color: #000;
}

.wrapper .bg-animate {
    position: absolute;
    top: -4px;
    right: 0;
    width: 850px;
    height: 600px;
    background: linear-gradient(45deg,#1097A3, #1097A3);
    transform: rotate(13deg) skewY(41deg);
    transform-origin: bottom right;
}


.wrapper .bg-animate2{
    position: absolute;
    top: -4px;
    left: 0;
    width: 850px;
    height: 600px;
    background: linear-gradient(45deg, #1097A3, #1097A3);
    transform: rotate(-10deg) skewY(-40deg);
    transform-origin: bottom left;
}
